import React from "react";
import Dropdown from 'react-bootstrap/Dropdown';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';

// import {NavLink} from 'react-router-dom';

export default function Header(props) {
  console.log(props);
  return (
    <div>
      <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark" fixed="top">
        <Navbar.Brand href="">Paola Frassinetti  </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link href="/home"> <i class="fas fa-home"></i> Home</Nav.Link>
            <Nav.Link href="/nosotros"> <i class="fas fa-pencil-ruler"></i> Nosotros</Nav.Link>
            <Nav.Link href="/contacto"><i class="fas fa-book"></i> Contacto</Nav.Link>
            <Dropdown>
              <Dropdown.Toggle variant="dark" id="dropdown-basic"> <i class="fas fa-cloud"></i>
                Informacion
              </Dropdown.Toggle>

              <Dropdown.Menu>
                <Dropdown.Item href="/blog">blog</Dropdown.Item>
                <Dropdown.Item href="#/action-2">Profesores</Dropdown.Item>
                <Dropdown.Divider />
                <Dropdown.Item href="#/action-3">Actividades</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
          </Nav>
          <Nav>
            <Nav.Link href="#deets">More deets</Nav.Link>
            <Nav.Link eventKey={2} href="#memes">
              Dank memes
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </div>
  );
}
